// import starting from the src/ folder
@import './color.scss';
@import './media.scss';

//style
* {
    margin: 0;
    padding: 0;
    font-family: inherit;
}

*:after,
*:before {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
.no-gutter > [class*='col-'] {
    padding-right: 0;
    padding-left: 0;
}

body {
    
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    // color: $lightGrey;
    letter-spacing: 0.8px;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow-x: hidden;
    &::-webkit-scrollbar-thumb {
        border-radius: 1px;
        background: rgba(1, 1, 1, 0.3);
    }
    &::-webkit-scrollbar {
        width: 10px;
    }   
}

@include mobile{
    .none-mobile{
        display: none;
    }
    .none-desktop{
        display: block !important;
    }
}
@include tablet{
    .none-mobile{
        display: none;
    }
    .none-desktop{
        display: block !important;
    }
}
@include desktop{
    .none-mobile{
        display: none;
    }
    .none-desktop{
        display: block !important;
    }
}
.none-desktop{
    display: none;
}

.container {
    max-width: 1366px;
    margin: 0 auto;
}
// Form login
.aff_dashboard {
    padding-top: 20vh;
}
.login-form {
    width: 100%;
}
.login-form-forgot {
  float: right;
}
.login-form-button {
  width: 100%;
}
.auto-center .ant-layout-content{
    display: flex;
    justify-content: center;
}
// Main layout
.mainLayout{
    & .ant-layout-content{
        min-height: 100vh !important;
    }
    & .trigger {
        font-size: 18px;
        line-height: 64px;
        padding: 0 24px;
        cursor: pointer;
        transition: color 0.3s;
        &:hover {
            color: #1890ff;
        }
    }
    & .ant-menu, & .ant-layout-sider{
        background: $black;
        color: $gray;
    }
    & .ant-menu-item a{
        color: $gray;
    }
    & .ant-menu-item-selected, & .ant-menu-item-selected a {
        background: $gray !important;
        color: $yellow !important;
    }
    .logo {
        background: rgba(255, 255, 255, 0.2);
        margin: 16px;
    }
} 
#editor iframe{
    min-height: 80vh !important;
    
}

// Layout authorization
#layOutAuthen{
    background: url(../images/bg/bg.jpeg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    max-height: 100vh;
    min-height: 100vh;
    overflow: hidden;
    #registerForm{
        & .ant-form-item{
            margin-bottom: 0;
            &:nth-last-child(2){
                margin-bottom: 24px;
            }
        }
    }
}
// upload img dragger
.uploadImg {
    & .ant-upload.ant-upload-select-picture-card{
        width: 100%;
        height: 100%;
        & .dragger{
            & .ant-upload-drag-icon {
                margin-bottom: 20px;
                & .anticon {
                    color: #40a9ff;
                    font-size: 48px;
                }
            }
            & .ant-upload-text {
                margin: 0 0 4px;
                color: rgba(0, 0, 0, 0.85);
                font-size: 16px;
            }
            & .ant-upload-hint {
                color: rgba(0, 0, 0, 0.45);
                font-size: 14px;
            }
        }
    }
    & .ant-upload-list-picture-card-container{
        width: 25%;
        height: 100%;
        float: none;
    }
    & .ant-upload-list-picture-card .ant-upload-list-item{
        width: 100%;
        height: 100%;
    }
}

// style card post
.cardPost{
    border-radius: .625rem !important;
    border: none !important;
    box-shadow: 0 2px 0 rgba(90,97,105,.11), 0 4px 8px rgba(90,97,105,.12), 0 10px 10px rgba(90,97,105,.06), 0 7px 70px rgba(90,97,105,.1);
    & .ant-card-cover{
        position: relative;
        
        & img{
            border-top-left-radius: .625rem;
            border-top-right-radius: .625rem;
            min-height: 10.3125rem;
            max-height: 10.3125rem;
            
        }
    }
    & .ant-card-head{
        top: .9375rem;
        right: .9375rem;
        position: absolute;
        text-transform: uppercase;
        z-index: 1;
        border-radius: 10rem;
        background-color: #00b8d8;
        padding: 0 1rem;
        border-bottom: none;
        min-height: auto;
        span{
            color: #fff;
            font-size: 12px;
        }
        & .ant-card-extra{
            padding: 5px 0;
        }
    }
    & .ant-card-meta-description{
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
    }
}

// Style editor
.design-web .container{
    max-width: 1366px !important;
}
// style preloader
.preloader{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #fffcf8;
    z-index: 99999; /* makes sure it stays on top */
  & .status{
    position: fixed;
    content: '';
    display: block;
    top: 25%;
    left: 0;
    right: 0;
    width: 400px;
    height: 300px;
    margin: 0 auto;
    background: url(../images/load.gif);
    opacity: 1;
    visibility: visible;
    -webkit-transition: all 3s ease-in-out;
    transition: all 3s ease-in-out;
    z-index: 9999999999;
  }
}
.loadData{
    text-align: center;
    background: rgba(0, 0, 0, 0.05);
    border-radius: 4px;
    margin-bottom: 20px;
    padding: 30px 50px;
    margin: 20px 0;
}
.form-item{
    & .ant-form-item-label{
        text-align: left;
    }
}